$fz:       14px;
// $fz-sm-px: 14px
// $fz-xs-px: 12px
// $fz-sm:    em(14)
// $fz-xs:    em(12)

$text-inputs: "[type='color']", "[type='date']", "[type='datetime']", "[type='datetime-local']", "[type='email']", "[type='month']", "[type='number']", "[type='password']", "[type='search']", "[type='tel']", "[type='text']", "[type='time']", "[type='url']", "[type='week']", "input:not([type])", "textarea";
$check-inputs: "[type='checkbox']", "[type='radio']";

// colors
$c-text:      #000;
$c-border:    #ebebeb;

$black:       #000;
$white:       #fff;
$light:       #f8f8f8;

$accent:      #3fb4ff;
$accent-aux:  #d9234a;
$accent-dark: #064e95;

// header heights
$header-top-height:    rem(38);
$header-bottom-height: rem(99);

$header-height:        $header-bottom-height + $header-top-height;

// border-radiuses
$radius:    5px;
$radius-sm: 3px;
$radius-lg: 10px;

// shadows
@mixin shadow {
  box-shadow: 0px 4px 16px 0px rgba(0, 0, 0, 0.28); }


@mixin windowHeight($offset: 0px) {
  height: calc(100vh - #{$offset});
  height: calc((var(--vh, 1vh) * 100) - #{$offset}); }
