.pagination {
  font-size: em(16);
  font-weight: 500;
  @include flex(flex-end, center);
  li {
    display: inline-block;
    @include notfirst {
      margin-left: rem(4); } }
  a {
    @include flex(center, center);
    background-color: #f4f4f4;
    height: rem(50);
    min-width: rem(50);
    border-radius: $radius-lg;
    @include tr(background-color .3s, opacity .3s, color .3s);
    @include hover {
      background-color: $accent;
      color: $white; } }
  .next, .prev {
    background-color: $accent;
    color: $white;
    @include hover {
      opacity: .7; }
    @include before {
      @include icon-arrow(1.1, 1.1, .1, currentColor, 45, em);
      display: block;
      margin-left: .4em; } }
  .next {
    transform: scaleX(-1); } }


