.nav {
  font-size: em(10.78);
  li {
    &:not(.footer-contact) {
      display: inline-block; }
    @include notlast {
      margin-right: rem(25); } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent; } }

  &--footer {
    li {
      &:not(.footer-contact) {
        display: block; }
      @include notlast {
        margin-right: 0;
        margin-bottom: .5em; } } } }
