.btn {
  display: inline-block;
  background-color: $accent-aux;
  border: 1px solid  $accent-aux;
  border-radius: $radius;
  color: $white;
  min-width: 190px;
  padding: .75em 1em;
  text-align: center;
  @include tr(opacity .3s);
  @include hover {
    opacity: .7; }

  &--transp-blue {
    background-color: transparent;
    color: inherit;
    min-width: 215px;
    border-color: $accent;
    padding: .5em 1em; } }
