.breadcrumbs {
  font-size: em(12);
  border: 1px solid  $c-border;
  background-color: #efefef;
  border-radius: $radius;
  padding: rem(8) rem(15);
  li {
    display: inline-block; }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent; } }
  &__item-current {
    color: $accent; } }
