.header-contact {
  $this: &;
  @include flex(flex-start, center);
  font-size: rem(15.5);
  &__icon {
    margin-right: .5rem;
    color: #dbdbdb;
    #{$this}--viber & {
      color: #8529ab; } }
  &__link {
    @include tr(color .3s);
    @include hover {
      color: $accent; } } }

.aside-contact {
  @include flex(flex-start, center);
  &__icon {
    color: #a8a8a8;
    font-size: 0;
    margin-right: .5rem;
    .icon {
      font-size: 1.2rem; } }
  &__link {
    @include tr(color .3s);
    @include hover {
      color: $accent; } } }

.aside-contacts {
  &__contact {
    @include notlast {
      margin-bottom: rem(10); } }
  &__block {
    padding-top: rem(8);
    padding-bottom: rem(8);
    @include notlast {
      border-bottom: 1px solid  $c-border; } } }

.footer-contact {
  @include flex(flex-start, center);
  .icon {
    fill: $accent;
    margin-right: .5em;
    font-size: 1.3em; }
  &--sm {
    .icon {
      font-size: 1em; } } }
