.services-card {
  $this: &;
  @include shadow;
  display: block;
  overflow: hidden;
  border-radius: $radius;
  background-color: $white;
  @include tr(background-color .3s, color .3s);
  @include hover {
    background-color: $accent;
    color: $white; }
  &__img {
    display: block;
    @include cover;
    background-color: $c-border;
    padding-top: percentage(240 / 260); }
  &__title {
    padding: .5em 1em;
    @include flex(center, center);
    text-align: center;
    height: rem(50);
    overflow: hidden; } }
