.items-tabs {
  $this: &;
  &__tabs {
    @include flex;
    margin-bottom: -.3em;
    position: relative;
    z-index: 0; }
  &__tab {
    font-size: em(16);
    padding: .7em 1.5em 1em;
    border-top-right-radius: $radius-lg;
    border-top-left-radius: $radius-lg;
    @include tr(background-color .3s, color .3s);
    @include hover {
      @extend #{$this}__tab--active; }
    &--active {
      background-color: $accent;
      color: $white; } }
  &__content {
    background-color: $white;
    border-radius: $radius;
    @include shadow;
    padding: rem(15);
    position: relative;
    z-index: 2; }
  &__item {
    @include hidden;
    overflow: hidden;
    max-height: 0;
    &--active {
      @include visible;
      max-height: none; } } }


